.slider {
  cursor: pointer;
  background-color: #f4f4f4;
  border: 1px solid #4440f6;
  border-radius: 30px;
  transition: all .2s;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: #4440f6;
  border-radius: 20px;
  transition: all .5s;
  position: absolute;
  top: 1px;
  left: 1px;
}

input:checked + .slider:before {
  background-color: #f4f4f4;
}

input:checked + .slider {
  background-color: #4440f6;
  border: 1px solid #f4f4f4;
}

input:focus + .slider {
  box-shadow: 0 0 1px #4440f6;
}

input:checked + .slider:before {
  transform: translateX(20px);
}

